.App {
  /* text-align: center; */
  font-family:'Hachi Maru Pop', cursive;
   overflow-x: hidden;
   color:rgba(218, 25, 105, 0.959);
}


.game-card{
    height: 500px;
    padding: 10px;
    background-color: aliceblue;
}
h1 {
    font-family: "Roboto";
    text-align: center;
    margin-bottom: 40px;
  }
  
  .timer-wrapper {
    display: flex;
    justify-content: right;
  }
  
  .timer {
    font-family: "Montserrat";
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .text {
    color: #aaa;
  }
  
  .value {
    font-size: 40px;
  }
  
  .info {
    max-width: 360px;
    margin: 40px auto 0;
    text-align: center;
    font-size: 16px;
  }
.ans-btn{
    background-color: rgb(255, 255, 255);
    border-radius: 4px;
    padding: 6px 28px 6px 28px;
    color: rgb(44, 40, 43);
    font-weight: bold;
    font-size: larger;
    border: 1px solid rgb(99, 96, 96);    
}

.correct-ans-btn{
    background-color: rgb(92, 212, 138);
    border-radius: 4px;
    padding: 6px 28px 6px 28px;
    color: rgb(247, 243, 246);
    font-weight: bold;
    font-size: larger;
    border: 1px solid rgb(255, 255, 255);    
}

.wrong-ans-btn{
    background-color: rgb(255, 72, 72);
    border-radius: 4px;
    padding: 6px 28px 6px 28px;
    color: rgb(247, 243, 246);
    font-weight: bold;
    font-size: larger;
    border: 1px solid rgb(255, 255, 255); 
}
